export class ApprenantDashboardComponent implements OnInit {
  enrollments: any[] = [];
  isLoading = true;
  completedCourses = 0;
  totalHours = 0;

  constructor(private http: HttpClient) {}

  ngOnInit() {
    this.loadEnrollments();
  }

  loadEnrollments() {
    this.http.get<any[]>('http://localhost:8080/api/enrollments/my-enrollments')
      .subscribe({
        next: (data) => {
          this.enrollments = data;
          this.completedCourses = data.filter(e => e.progress === 100).length;
          this.totalHours = data.reduce((sum, e) => sum + (e.formation.duration || 0), 0);
          this.isLoading = false;
        },
        error: () => {
          this.isLoading = false;
        }
      });
  }
}
